body {
    margin:0;
    padding:0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #f3f3f3;
    height: 100%;
}

html, body, #potato, .container{
    height: 100%;
}

.loader{
    width:100%;
    height:100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 50px;
    width:80%;
}

.movies .movie{
    width:45%;
    background-color:white;
    margin-bottom: 70px;
    display:flex;
    align-items: flex-end;
    justify-content: space-between;
}

.movie img{
    position: relative;
    top: -50px;
    width:100%;
    max-width: 120px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25), 0 18px 36px -18px rgba(0, 0, 0, 0.3), 0, -12px 36px -8px rgba(50, 50, 93, 0.025);
}

.movie .movie_title, 
.movie .movie_year{
    margin: 0;
    font-weight: 300;
}

.movie_genres{
    list-style: none;
}